import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-badge.scss';

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

const _round = (value) => {
	if (isNaN(value) || value >= 1e15) {
		return value;
	}
	let newValue = value;
	const suffixes = ['', 'K', 'M', 'B', 'T'];
	let suffixNumber = 0;
	while (newValue >= 1000) {
		newValue /= 1000;
		suffixNumber++;
	}
	newValue = parseFloat(newValue.toFixed(1));
	newValue += suffixes[suffixNumber];
	return newValue;
};

const view = (state) => {
	const {variant, size, status, value, round} = state.properties;
	const displayValue = round ? _round(value) : value;
	return (
		<span
			class={{
				'now-badge': true,
				['-' + variant]: variant,
				['-' + status]: status,
				['-' + size]: size
			}}>
			<span className="now-line-height-crop">{displayValue}</span>
		</span>
	);
};

/**
 * A badge is an indicator or symbol that displays pending notifications, a
 * change in the systemâs state, or an event.
 *
 * Badge notifications indicate a change in system status and aren't tied to any user input or
 * current user activity.
 *
 * ```jsx
 * <now-badge variant="primary" size="lg" status="warning" value="99" round>
 * </now-badge>
 * ```
 *
 * @seismicElement now-badge
 * @summary Numeric badge that indicates a total count.
 * @uib.label Badge
 * @uib.icon badge
 * @uib.description Numeric badge that indicates a total count.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties variant, status, size, value, round
 */
createEnhancedElement('now-badge', {
	properties: {
		/**
		 * Sets the badge style.
		 * @type {('primary'|'secondary')}
		 * @uib.label Style
		 * @uib.description Style of the badge
		 */
		variant: {
			default: 'primary',
			schema: {type: 'string', enum: ['primary', 'secondary']}
		},
		/**
		 * Sets the badge status color.
		 * @type {('critical'|'high'|'warning'|'moderate'|'info'|'positive'|'low')}
		 * @uib.description Status color for the badge
		 */
		status: {
			default: 'low',
			schema: {
				type: 'string',
				enum: [
					'critical',
					'high',
					'warning',
					'moderate',
					'info',
					'positive',
					'low'
				]
			}
		},
		/**
		 * Sets the badge size.
		 * @type {('sm'|'md'|'lg')}
		 */
		size: {default: 'md', schema: {type: 'string', enum: ['sm', 'md', 'lg']}},
		/**
		 * Number to display in the badge.
		 * @type {number}
		 * @uib.defaultValue 5
		 */
		value: {required: true, schema: {type: 'number'}},
		/**
		 * When true, rounds up and uses the abbreviations 'K', 'M', 'B', and 'T' to represent large numbers.
		 * For example, instead of '1000', the system displays '1K'. Instead of '5376000', the system displays '5.4M'.
		 * @type {boolean}
		 * @uib.label Round large numbers
		 * @uib.description Whether to round up large numbers (between thousands and trillions)
		 * @uib.defaultValue true
		 */
		round: {schema: {type: 'boolean'}},
		/**
		 * Deprecated for WCAG compliance. Badge consumers are responsible
		 * for representing the badge content and context to all users accessibly.
		 * @private
		 */
		description: {}
	},
	view,
	styles
});
